<template>
    <div class="image-upload">
        <el-upload
        class="image-upload"
        drag
        action="/api/images"
        :on-success="uploaded"
        multiple
        :limit="5"
        list-type="picture">
            <el-button size="small" type="primary">Click to upload</el-button>
            <div slot="tip" class="el-upload__tip">jpg/png files with a size less than 500kb</div>
        </el-upload>
    </div>
</template>

<script>
    export default {
        name: "image-upload",
        methods: {
            uploaded(response) {
                this.$emit('uploaded', response)
            }
        }
    }
</script>